*{
	font-size: 16px;
}
.lhs{
	width: 50%;
	float: left;
}
.rhs{
	width: 50%;
	float: left;
}
h2{
	font-size: 26px;
	text-align: center;
}
h3{
	font-size: 24px;
}
.content{
	padding: 10px;
	margin: 10px;
	background-color: bisque;
}
hr{
	clear: both;
}
#out{
	border: 2px solid;
	padding: 10px;
}
pre{
	tab-size: 2;
	background: lightgray;
/*	border: 2px solid;*/
	padding: 10px;
	border-radius: 20px;
	white-space: pre-wrap;
}
.nav-item{
	cursor: pointer;
}
#main-form{
	width: 100%;
	td:first-child{
		width: 100px;
	}
	input{
		width: 100%;
	}
	input{
		width: 85%;
	}
}
.removeIcon{
	cursor: pointer;
	:hover{
		color: red;
		background-color: skyblue;
		border-radius: 20px;
	}
}
@media(max-width:991px){
	.lhs,.rhs,#sticky-sidebar,#main{
		width: 100%;
	}
	#main-form select {
		width: 70px;
	}
}
@media(min-width:768px){
	#main-form select {
		width: 100%;
	}
}